.main-div{
    width: 350px;
    height: 250px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    /* border: 2px solid black; */
    background-color: white;
    z-index: 1;
    background: url('./main-div-background.png') no-repeat center center fixed;
    background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
}

.nav{
    width: 100%;
    height: 50px;
    /* border: 2px solid white; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-bottom: 20px;
    position: relative;
    /* top: 20px; */
    background-color: rgba(0, 0, 0, 0.7);
    padding: 0 10px;
}

.app-name{
    color: white;
    margin-right: 110px;
    margin-bottom: 5px;
    text-transform: capitalize;
    font-size: 25px;
}

.menu{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}
.smiley{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.message-div{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    padding-left: 5px;
    padding-right: 5px;
}

.message{
    color:white;
    text-transform: capitalize;
    font-weight: 1000;
    font-size: 25px;
}

form{
    width: 95%;
    height: 42px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    border-radius: 10px;
    margin-top: 40px;
    border: 1px solid black;
    background-color: white;
}

.popular-recipes{
    font-size: 25px;
    text-align: left;
    text-transform: capitalize;
    padding-left: 5px;
    /* border: 2px solid black; */
    color: white;
    margin-top: 60px;
    font-weight: bolder;
    width: 100%;
    text-align: left;

}

/* .search-bar:focus{
    border: 0.5px solid mediumseagreen;
} */

.search-bar{
    width: 98%;
    height: 100%;
    /* border: 2px solid blue; */
    border-top-left-radius: inherit;
    border-bottom-left-radius: inherit;
    border: 0;
    box-shadow: 0;
    padding-left: 10px;
    text-transform: capitalize;
    outline: none;
}

.search-icon{
    width: 20px;
    height: 20px;
    margin-right: 12px;
    cursor: pointer;
    /* border: 2px solid black; */
}





