*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  width: 100vw;
  height: inherit;
  font-family: poppins, hubballi, open-sans, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.index{
  width: 100vw;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  /* background: linear-gradient(to right, rgb(252, 3, 78), rgb(248, 6, 155)); */
  overflow: hidden;
  background: url('./index-wallpaper.png') no-repeat center center fixed;
  background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-color: rgba(128, 128, 128, 0.7);
  background-blend-mode: overlay;
}









