.popular-recipe-card{
    width: 98%;
    height: 120px;
    /* border-radius: 10px; */
    border-bottom: 0.5px solid black;
    /* border: 0; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 0 5px;
    cursor: pointer;
    margin: 10px 0;
}

.recipe-image-div{
    width: 30%;
    height: 110px;
    border-radius: 10px;
}

.recipe-image{
    width: 100%;
    height: inherit;
    border-radius: inherit;
}

.recipe-description{
    width: 68%;
    height: 98%;
    /* border: 0.5px solid black; */
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: left;
    flex-direction: column;
    padding: 2px 10px;
}

.dish-cuisine-type{
    width: 95%;
    height: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    /* border: 0.5px solid black; */
}

.meal-type, .cuisine-type, .dish-type{
    font-size: 10px;
    text-transform: capitalize;
}

.recipe-name{
    font-size: 12px;
    margin-top: 30px;
    text-transform: capitalize;
    font-weight: bolder;
}