.app-main{
    width: 350px;
    height: 680px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.8);
    /* border: 2px solid white; */
    margin-top: 20px;
    overflow: hidden;
    /* overflow: scroll; */
    border-radius: 20px;
    scroll-behavior: smooth;
    padding: 0 10px;
    box-shadow: 15px 15px 50px black;
}

.container{
    width: 350px;
    min-height: 680px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background-color: rgba(255, 255, 255, 0.8);
}

.body-section{
    height: 680px;
    width: 350px;
    /* background-color: white; */
}
